
*,::after,::before{margin:0;padding:0;-webkit-box-sizing:border-box;box-sizing:border-box;font-family:"Montserrat", sans-serif
}
body{background:url(/static/img/background.b131f68.jpg) no-repeat;background-size:cover
}

#header[data-v-1e3d0c34]{display:grid;grid-template-columns:1fr 5fr 1fr;grid-template-rows:1fr;width:100%;height:60px;justify-items:center;overflow:hidden;background-color:white
}
#logo[data-v-1e3d0c34]{width:100px;height:40px;margin-top:10px;justify-self:right
}
nav[data-v-1e3d0c34]{list-style:none;height:100%;text-align:center
}
nav li[data-v-1e3d0c34]{cursor:pointer;display:inline-block;padding:20px;margin:0;margin-left:20px;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out
}
nav li[data-v-1e3d0c34]:hover{background-color:#ffd110
}
ul[data-v-1e3d0c34]{list-style:none;text-align:center
}
ul li[data-v-1e3d0c34]{cursor:pointer;display:inline-block;margin-top:2vh;padding-left:10px
}
ul li ion-icon[data-v-1e3d0c34]{font-size:30px
}

#container[data-v-e0865eb0]{height:90vh;width:100%;display:grid;grid-template-columns:1fr 1fr
}
#slogan[data-v-e0865eb0]{display:block;width:80%;margin:20vh 10vw;color:#fafafa
}
#slogan h1[data-v-e0865eb0]{font-size:40px;font-weight:bold;text-shadow:2px 2px 11px #000000
}
#slogan p[data-v-e0865eb0]{margin-top:10px;font-size:16px;font-style:italic
}
#slogan button[data-v-e0865eb0]{margin-top:30px;margin-bottom:10px;width:150px;height:60px;color:white;font-weight:bold;background-color:#841818;border-radius:10px;border:none;cursor:pointer;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out
}
#slogan button[data-v-e0865eb0]:hover{background-color:#611a1a
}
#slogan a[data-v-e0865eb0]{color:white;font-weight:bold;font-size:14px;font-style:italic
}
.slider[data-v-e0865eb0]{display:grid;grid-template-columns:1fr 2fr 1fr;grid-template-rows:1fr;overflow:hidden;-webkit-box-align:center;-ms-flex-align:center;align-items:center;justify-items:center;width:100%;height:100%
}
#right-arrow[data-v-e0865eb0],#left-arrow[data-v-e0865eb0]{cursor:pointer;border-radius:50%;-webkit-transform:scale(0.5);transform:scale(0.5);justify-self:left
}
#left-arrow[data-v-e0865eb0]{-webkit-transform:scale(0.5) rotate(180deg);transform:scale(0.5) rotate(180deg);float:left;justify-self:right
}
.image-wrapper[data-v-e0865eb0]{overflow:hidden;width:100%
}
#image-container img[data-v-e0865eb0]{border:5px solid white
}
